import Link from 'next/link';
import { request } from 'graphql-request';

// const URL = 'https://server-dubymzzcjd.now.sh/graphql'
const URL = 'http://localhost:4000/graphql';

const Post = props => (
  <div>
    <h1>{props.route.routeName}</h1>
    <ul>
      {props.route.sailings.map(sailing => (
        <li key={sailing.id}>
          {sailing.vessel} -{' '}
          {new Date(sailing.scheduledDeparture).toISOString()} -{' '}
          {sailing.actualDeparture} - {sailing.eta} - {sailing.sailingStatus}
        </li>
      ))}
    </ul>
  </div>
);

Post.getInitialProps = async function(context) {
  const routeName = context.query.id;
  const gqlq = `{
      route(routeName: "${routeName}"){
          routeName
          sailings {
              id
              vessel
              scheduledDeparture
              actualDeparture
              eta
              sailingStatus
          }
      }
  }`;
  const res = await request(URL, gqlq);
  console.log(`Route info fetched. Sailings: ${res.route.sailings.length}`);
  return res;
};

export default Post;



